

.headExerciseWrap{
    padding: 0 15px;
    width: fit-content;
    margin: auto;
}
.descExercise h5{
    color:rgb(17, 104, 17);
    margin-left: 50px;
}
.descExercise p{
    padding: 0 15px;
}
.headExerciseWrap>div{
    display: inline-block;
    padding: 15px 15px;
}
.headExerciseWrap label{
    margin:0 30px 0 10px;
    font-size: 18px;
}
.headExerciseWrap select{
    font-size: 18px;
}
.desc{
    position: relative;
}
.nameCat {
    display: inline-block;
    position:absolute;
    top: 0;
    left: 18px;
    color:rgb(17, 104, 17);
}
.nameCat h5{
    margin: 0;
    padding: 0;
}

@media only screen and (max-width: 640px){
    .headExerciseWrap label{
        font-size: 4vw;
    }
    .headExerciseWrap select{
        font-size: 4vw;
    }
}