
.subCatA1 .selected{
    font-weight: bold;
}
.subCatA1{
    box-sizing: border-box;
    position: relative;
    box-shadow: 0 28px 28px rgb(0 0 0 / 20%);
}
.subCatA1 .checked{
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(13, 71, 71, 0.1);
}
.subCatA1 h5{
    padding: 0px 15px;
    margin: 15px 0;
}
.subCatA1 .block{
    box-sizing: border-box;
    display: inline-block;
    width: 50%;
    padding: 0 20px;
    /* border: 2px solid green;
    border-top: none; */
}

.subCatA1 .row{
    /* border: 2px solid green; */
    border-radius: 15px;
    border: 5px solid rgb(0 100 100);
}
.subCatA1 .points{
    position: absolute;
    top:-165px;
    left: 3px;
    background-color: rgba(66, 221, 221, .4);
    padding: 10px;
}
.subCatA1 .template{
    background-color: rgba(66, 221, 221, .4);
    padding-top: 10px;
}
.subCatA1 .template h5{
    margin: 0;
}

@media only screen and (min-width: 640px) {
    .subCatA1 .block:last-child{
        /* border-left:2px solid green; */
        border-left:5px solid rgb(0 100 100)
    }
    .subCatA1 .row{
        border-bottom: none;
    }
    .subCatA1 .row:last-child{
        /* border-bottom:2px solid green;  */
        border-bottom:5px solid rgb(0 100 100)
    }
    /* .template{
        border-left: 2px solid green;
    } */
  }
@media only screen and (max-width: 640px) {
    .subCatA1 .block {
      display: block;
      width: 100%;
      /* border-bottom:2px solid green; */
      border-bottom:5px solid rgb(0 100 100)
    }
    .subCatA1 .row{
        border: none;
    }
    .subCatA1 .row:first-child{
        /* border-top:2px solid green; */
        border-top:5px solid rgb(0 100 100)
    }
    .subCatA1 .points{
        position: unset;
        padding: 20px 0;
        background-color:unset;
    }
  }

