
.navExercise div{
    width: fit-content;
    margin: auto;
}
.navExercise button{
   font-size: 30px;
   border-radius: 8px;
   margin-right: 10px;
   background-color: rgba(66, 221, 221, .4);
}
.navExercise button:hover{
   cursor: pointer;
   background-color: rgba(13, 71, 71, 0.4);
}
.navExercise button:disabled{
   cursor: default;
   background-color: rgba(66, 221, 221, .4);
}
.navExercise i{
   font-size: 30px;
}
.navExercise a{
   text-decoration: none;
   color: #000;
}