
.headerStatistic h5{
    width: fit-content;
    margin: 15px auto;
}
.headerStatistic table{
    width: 100%;
    margin: 15px auto;
}
.headerStatistic table tr:nth-child(even){
    background-color: #f2f2f2;
}
#moreStatistic h5{
    width: fit-content;
    margin: 15px auto;
}
#moreStatistic span{
    text-transform: lowercase;
}
#moreStatistic table{
    width: 100%;
    margin: 15px auto;
    border: 1px solid black;
    border-collapse: collapse;
}
#moreStatistic table tr:nth-child(even){
     background-color: #f2f2f2;
}
#moreStatistic table td:last-child{
    width: 100px;
}
#moreStatistic{
    max-height: 0;
    overflow-y: hidden;
    -webkit-transition: max-height 1s; 
  -moz-transition: max-height 1s; 
  -ms-transition: max-height 1s; 
  -o-transition: max-height 1s; 
  transition: max-height 1s; 
}
#moreStatistic.active{
    overflow-y:auto;
    max-height: 1000px;
    /* background-color: cadetblue; */
    -webkit-transition: max-height 1s; 
  -moz-transition: max-height 1s; 
  -ms-transition: max-height 1s; 
  -o-transition: max-height 1s; 
  transition: max-height 1s; 
}