    width: 100px;
}
#moreStatistic{
    max-height: 0;
    overflow-y: hidden; 
  transition: max-height 1s; 
}
#moreStatistic.active{
    overflow-y:auto;
    max-height: 1000px;
    /* background-color: cadetblue; */ 
  transition: max-height 1s; 
}

