:root {
    --white: #fff;
    --active-color: #00aeb0;
  }
  
  * {
    box-sizing: border-box;
  }
  
  /* html {
    font-family: Arial, Helvetica, sans-serif;
    height: 100%;
    background: var(--active-color);
    transition: background 0.4s ease;
  } */
  
  .audio-player {
    font-family: Arial, Helvetica, sans-serif;
    /* height: 100%; */
    /* background: var(--active-color); */
     /* max-width: 350px; */
     padding: 24px;
     border-radius: 20px;
     box-shadow: 0 28px 28px rgba(0, 0, 0, 0.2);
     /* margin: auto; */
     margin-bottom: 20px;
     color: #fff;
     color: var(--white);
    background-color: rgba(0, 170, 170, .4);
    border: 5px solid rgb(0 100 100);
    transition: background 0.4s ease;
  }
  
  .audio-player button {
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .audio-player input[type="range"] {
    height: 5px;
    -webkit-appearance: none;
    width: 100%;
    margin-bottom: 10px;
    border-radius: 8px;
    background: #3b7677;
    transition: background 0.2s ease;
    cursor: pointer;
  }
  
  .artwork {
    border-radius: 120px;
    display: block;
    margin: auto;
    height: 200px;
    width: 200px;
  }
  
  .track-info {
    text-align: center;
    z-index: 1;
    position: relative;
  }
  
  .title {
    font-weight: 700;
    margin-bottom: 4px;
  }
  
  .artist {
    font-weight: 300;
    margin-top: 0;
  }
  
  /* .audio-controls {
     width: fit-content;
  } */
/*   
  .audio-controls {
    display: flex;
    justify-content: space-between;
    width: 75%;
    margin: 0 auto 15px;
  } */
  #butPlay{
    border-radius: 15px;
    border: 3px solid rgb(0 100 100);
    min-width: 160px;
    margin: 0 5px;
  }
  .audio-controls {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 0 auto 10px;
  }
  .audio-controls div p{
      font-size: 35px;
      margin: 0;
      padding: 5px;
  }
  #duration{
    border-radius: 15px;
    border: 3px solid rgb(0 100 100);
    margin: 0 5px;
  }
  #playTimer{
    min-width: 160px;
    /* height: fit-content; */
    /* padding: 5px; */
    border-radius: 15px;
    border: 3px solid rgb(0 100 100);
    margin: 0 5px;
  }
  
  .audio-controls .prev svg,
  .audio-controls .next svg {
    width: 35px;
    height: 35px;
  }
  
  .audio-controls .play svg,
  .audio-controls .pause svg {
    height: 40px;
    width: 40px;
  }
  
  .audio-controls path {
    fill: #fff;
    fill: var(--white);
  }
  
  .color-backdrop {
    background: linear-gradient(45deg, #00aeb0 20%, transparent 100%)
      no-repeat;
    background: linear-gradient(45deg, var(--active-color) 20%, transparent 100%)
      no-repeat;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }
  
  .color-backdrop.playing {
    -webkit-animation: colorChange 20s alternate infinite;
            animation: colorChange 20s alternate infinite;
  }
  
  @-webkit-keyframes colorChange {
    from {
      -webkit-filter: hue-rotate(0deg);
              filter: hue-rotate(0deg);
    }
    to {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }
  
  @keyframes colorChange {
    from {
      -webkit-filter: hue-rotate(0deg);
              filter: hue-rotate(0deg);
    }
    to {
      -webkit-filter: hue-rotate(360deg);
              filter: hue-rotate(360deg);
    }
  }

  @media only screen and (max-width: 600px) {
    #butPlay{
      min-width:unset;
    }
    #duration p{
      font-size:5.5vw;
    }
    #playTimer{
      min-width:unset;
    }
    #playTimer p{
      font-size:5.5vw;
    }
  }
  

.subCatA1 .selected{
    font-weight: bold;
}
.subCatA1{
    box-sizing: border-box;
    position: relative;
    box-shadow: 0 28px 28px rgb(0 0 0 / 20%);
}
.subCatA1 .checked{
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(13, 71, 71, 0.1);
}
.subCatA1 h5{
    padding: 0px 15px;
    margin: 15px 0;
}
.subCatA1 .block{
    box-sizing: border-box;
    display: inline-block;
    width: 50%;
    padding: 0 20px;
    /* border: 2px solid green;
    border-top: none; */
}

.subCatA1 .row{
    /* border: 2px solid green; */
    border-radius: 15px;
    border: 5px solid rgb(0 100 100);
}
.subCatA1 .points{
    position: absolute;
    top:-165px;
    left: 3px;
    background-color: rgba(66, 221, 221, .4);
    padding: 10px;
}
.subCatA1 .template{
    background-color: rgba(66, 221, 221, .4);
    padding-top: 10px;
}
.subCatA1 .template h5{
    margin: 0;
}

@media only screen and (min-width: 640px) {
    .subCatA1 .block:last-child{
        /* border-left:2px solid green; */
        border-left:5px solid rgb(0 100 100)
    }
    .subCatA1 .row{
        border-bottom: none;
    }
    .subCatA1 .row:last-child{
        /* border-bottom:2px solid green;  */
        border-bottom:5px solid rgb(0 100 100)
    }
    /* .template{
        border-left: 2px solid green;
    } */
  }
@media only screen and (max-width: 640px) {
    .subCatA1 .block {
      display: block;
      width: 100%;
      /* border-bottom:2px solid green; */
      border-bottom:5px solid rgb(0 100 100)
    }
    .subCatA1 .row{
        border: none;
    }
    .subCatA1 .row:first-child{
        /* border-top:2px solid green; */
        border-top:5px solid rgb(0 100 100)
    }
    .subCatA1 .points{
        position: unset;
        padding: 20px 0;
        background-color:unset;
    }
  }



