
.subCatA3 input{
    font-weight: bold;
    font-size: 15px;
    border: none;
    margin: 0 5px;
    text-align: center;
    background: rgb(228, 213, 14, .2);
}
.subCatA3 p span:first-child{
    margin-left: 50px;
}
.subCatA3 p{
    margin:0;
}
.subCatA3 #hint{
    text-decoration: underline;
}
.subCatA3 span{
    line-height: 2.5em;
}
.subCatA3 .spanBlock{
    position: relative;
    display: inline-block;
}
.subCatA3 .hint{
    position: absolute;
    top:-20px;
    right: 10px;
    display: none;
}
.subCatA3 input::placeholder{
    text-align: center;
}
