@import url('https://fonts.googleapis.com/css2?family=Lato:wght@900&family=Roboto:ital,wght@1,500&display=swap');

html{
    height: 100%;
    box-sizing: border-box;
}
body{
	font-family: 'Roboto', sans-serif;
    color: #000;
	min-width: 320px;
    height: 100%;
}
img{
	max-width: 100%;
	height: auto;
}
h1{
	font-family: 'Lato', sans-serif;
	font-size: 80px;
	font-weight: 500;
}
h2{
	font-family: 'Lato', sans-serif;
	font-size: 50px;
	font-weight: 500;
}
h3{
    font-family: 'Lato', sans-serif;
	font-size: 40px;
}
h4{
	font-family: 'Lato', sans-serif;
	font-size: 30px;
}
h5{
	font-family: 'Lato', sans-serif;
	font-size: 18px;
}